import { Box, Button, TextField, Typography } from '@mui/material';
import React, { useState } from 'react';
import { useAppDispatch } from '../../../store';
import { createTeam } from './teamsSlice';
import NotificationSnackbar from '../../../components/utils/NotificationSnackbar';

export default function CreateNewTeam({
  closeAddNewTeam,
}: {
  closeAddNewTeam: any;
}): JSX.Element {
  const dispatch = useAppDispatch();
  const [fullTitleRu, setFullTitleRu] = useState<string>('');
  const [fullTitleEn, setFullTitleEn] = useState<string>('');
  const [country, setCountry] = useState<string>('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [snackbarSeverity, setSnackbarSeverity] = useState<
    'success' | 'error' | 'warning' | 'info'
  >('info');
  const [snackbarMessage, setSnackbarMessage] = useState('');

  const handleSaveClick = React.useCallback(async () => {
    const dispatchResult = await dispatch(
      createTeam({ fullTitleRu, fullTitleEn, country })
    );
    if (createTeam.fulfilled.match(dispatchResult)) {
      setOpenSnackbar(true);
      setSnackbarSeverity('success');
      setSnackbarMessage('  ');
      setFullTitleRu('');
      setFullTitleEn('');
      setCountry('');
    }
    if (createTeam.rejected.match(dispatchResult)) {
      setOpenSnackbar(true);
      setSnackbarSeverity('error');
      if (dispatchResult.error.message) {
        setSnackbarMessage(dispatchResult.error.message);
      }
    }
  }, [dispatch, country, fullTitleEn, fullTitleRu]);

  const handleFullTitleRuChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ): void => {
    setFullTitleRu(event.target.value);
  };

  const handleFullTitleEnChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ): void => {
    setFullTitleEn(event.target.value);
  };

  const handleCountryChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setCountry(event.target.value);
  };

  const handleCancelClick = (): void => {
    closeAddNewTeam(false);
  };

  const handleCloseSnackbar = (): void => {
    setOpenSnackbar(false);
  };

  return (
    <Box>
      <Box sx={{ my: 1 }}>
        <TextField
          fullWidth
          required
          id="team-name-ru"
          label=" (RU)"
          variant="outlined"
          value={fullTitleRu}
          onChange={handleFullTitleRuChange}
        />
      </Box>
      <Box sx={{ my: 1 }}>
        <TextField
          fullWidth
          required
          id="team-name-en"
          label=" (EN)"
          variant="outlined"
          value={fullTitleEn}
          onChange={handleFullTitleEnChange}
          helperText="  (EN)       (      )"
        />
      </Box>
      <Box sx={{ my: 1 }}>
        <TextField
          fullWidth
          required
          id="team-country"
          label=" "
          variant="outlined"
          value={country}
          onChange={handleCountryChange}
          helperText="    3  ( ENG, GER...)"
        />
      </Box>
      <Box sx={{ my: 2 }}>
        <Button
          sx={{ height: '1.8rem', px: 1, mr: 1 }}
          variant="contained"
          color="error"
          onClick={handleCancelClick}
        >
          <Typography
            variant="button"
            fontWeight="600"
            fontSize="0.9rem"
            fontFamily="Shantell Sans"
          >
            
          </Typography>
        </Button>
        <Button
          onClick={handleSaveClick}
          sx={{ height: '1.8rem', px: 1 }}
          variant="contained"
          color="success"
        >
          <Typography
            variant="button"
            fontWeight="600"
            fontSize="0.9rem"
            fontFamily="Shantell Sans"
          >
            
          </Typography>
        </Button>
      </Box>
      <Box textAlign="center">
        <NotificationSnackbar
          open={openSnackbar}
          onClose={handleCloseSnackbar}
          severity={snackbarSeverity}
          message={snackbarMessage}
          duration={3000}
        />
      </Box>
    </Box>
  );
}
